#resume {
    padding-top: 50px; /* 헤더의 높이만큼 body에 패딩을 추가합니다. */
    margin-top: 100px; // 상단 여백 설정 예시

    .resume__title {
        text-align: center; /* 텍스트를 가운데 정렬 */
        font-size: 2.5rem; /* 글자 크기 조정 */
        font-weight: bold; /* 글자 두껍게 설정 */
        margin-bottom: 20px; /* 아래 여백 추가 */
    }
    
    .resume__inner {
        flex-direction: column;
        height: 100%;
        width: 80%;
        max-width : 1900px;
        margin: 0 auto  ; // 좌우 가운데 정렬

        padding: 16px; /* 내부 콘텐츠와 경계(여기서는 안쪽 여백) 사이의 공간을 설정 */
        display: flex;
        justify-content: space-between;
        
        .education, .experience, .license, .skills {
            background-color: #000000; /* 까만색 배경 */
            color: #ffffff; /* 흰색 글씨 */
            padding: 10px; /* 내부 여백 */
            margin-bottom: 80px; /* 아래 여백 */
            
            h2 {
                text-align: center; /* 텍스트를 가운데 정렬 */
                font-size: 1.8rem; /* 글자 크기 조정 */
                font-weight: bold; /* 글자 두껍게 설정 */
                margin-top: 20px;
                margin-bottom: 50px; /* 아래 여백 추가 */
            }
            
            &_1 {
                div {
                    margin-bottom: 20px; /* 원하는 간격(px)으로 설정 */
                    text-align: center; /* 텍스트를 가운데 정렬 */
                    font-size: 1.4rem; /* 글자 크기 조정 */
                }
            }
        }
    }
}
