#footer {
    display: block;
    font-family: var(--mainKor-font);

  
  .footer__inner {
    padding: 0.5vw;
  }
  
  .footer__info {
    display: flex;
    justify-content: space-between;
    margin: 40px 0;
  }
  
  .left-side {
    width: 50%; // 자동으로 왼쪽 50% 배치
    flex: 0.8; // 좌측 영역이 남은 공간을 모두 차지하도록 설정
    font-size: 1em;
    margin-left: 40px;
    display: flex; // 자식 요소들을 행으로 배치하기 위해
    align-items: center; // 세로 중앙 정렬을 위해
    justify-content: space-between; // 좌우 정렬을 위해
  }
  
  .right-side {
    display: flex;
    flex-direction: row; // 우측 영역을 가로로 나열
    flex: 1.2; // 우측 영역이 남은 공간을 모두 차지하도록 설정
  
    .right-row {
      flex: 1; // 각 우측 컬럼이 세로 방향으로 남은 공간을 모두 차지하도록 설정
      margin: 0 10px; // 각 컬럼 사이의 여백 설정
      font-size: 0.6rem;
      align-content: center;
  
      span {
        font-size: 1.6em;
        padding: 0.1rem 1rem;
        border-right: 0.2rem solid #e25252;
      }
  
      .contact-info {
        font-size: 1em; // 작은 글씨 사이즈
      }
    }
  }
  
  .contact-info {
    display: inline-block; // div를 인라인 블록 요소로 설정하여 옆에 다른 인라인 요소가 올 수 있게 함
    vertical-align: bottom; // 상단 정렬 (선택적으로 필요할 때 설정)
    margin-left: 10px; // div와 p 사이의 여백 설정
  }
  
  .footer-icons {
    transform: translateY(-100%);
  }
}